    stroke-linecap: round;
}

.axis .halo {
    fill: none;
    stroke: #ddd;
    stroke-width: 7.5px;
    stroke-linecap: round;
}

.slider .handle {
    /*fill: #fff;*/
    stroke: #111;
    stroke-opacity: .5;
    stroke-width: 1.25px;
    pointer-events: none;
}

.dIntroText {
    text-anchor: middle;
    fill: #282828;
    cursor: pointer;
}

a.dIntroText {
    font-family: sans-serif;
    font-size: 24px;
    text-decoration: inherit;
    color: black;
}

div#intro div * {
    font-family: sans-serif;
    font-size: 13px;
    font-style: italic;
}

.dDataDropZone {
    height: 500px;
    float: right;
    margin: 10px;
    border: 3px solid #666;
    width: 50%;
    opacity: 1.0;
}

.dDataDropZone svg {
    width: 100%;
}

.dDataDropZone.dropping {
    opacity: 0.3;
    box-shadow: inset 0px -15px 50px 50px rgba(40, 40, 40, 1);
}

div#chartGroup {
    position: absolute;
    width: 720px;
    margin: 0 auto;
    bottom: 0%;
    left: 50%;
    /* -webkit-transform: translateX(-50%); */
}

svg.dataChart path.chartLine {
    fill: none;
    shape-rendering: auto;
    stroke: #000;
    stroke-width: 1px;
}

svg.dataChart circle.chartCircle {
    fill: rgb(222, 45, 38);
    stroke-width: 1;
    stroke: #000;
}

svg.dataChart text.chartCircleText {
    font: 8pt sans-serif;
    fill: rgb(222, 45, 38);
    text-shadow: 1px 1px 0 #000, 0px 1px 0 #000;
}

svg.dataChart g.chartAxis text {
    font: 8pt sans-serif;
    fill: #555;
}

svg.dataChart g.chartAxis path {
    display: none;
}

svg.dataChart g.chartAxis line {
    fill: none;
    stroke: #555;
    opacity: 0.4;
    shape-rendering: crispEdges;
}

.slidecontainer {
    width: 100%;
    /* Width of the outside container */
}

/* The slider itself */
.slider {
    -webkit-appearance: none;
    /* Override default CSS styles */
    appearance: none;
    height: 11px;
    /* Specified height */
    background: #d3d3d3;
    /* Grey background */
    outline: none;
    /* Remove outline */
    width: 80%;
    display: block;
    position: unset;
    margin-top: 6px;
    margin-left: 10px;
    margin-right: 10px;
    float: left;
}

/* Mouse-over effects */
.slider:hover {
    opacity: 1;
    /* Fully shown on mouse-over */
}

.slider:before {
    display: none !important;
}

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    /* Override default look */
    appearance: none;
    width: 25px;
    /* Set a specific slider handle width */
    height: 25px;
    /* Slider handle height */
    background: #4CAF50;
    /* Green background */
    cursor: pointer;
    /* Cursor on hover */
    border-radius: 50%;
}

.slider::-moz-range-thumb {
    width: 25px;
    /* Set a specific slider handle width */
    height: 25px;
    /* Slider handle height */
    background: #4CAF50;
    /* Green background */
    cursor: pointer;
    /* Cursor on hover */
    border-radius: 50%;
}

.dDataDropZone {
    margin: inherit !important;
    width: 65% !important;
}

div#video_player_box {
    margin: inherit !important;
    margin-bottom: 55px !important;
    width: 35% !important;
}

.map-botton {
    padding: 10px 14px;
    border-radius: 24px;
    position: absolute;
    right: 0;
    margin: 15px 16px 0px 0px;
    bottom: 6px;
    z-index: 999;
}</style>
   